@import url(https://fonts.googleapis.com/css2?family=Georama:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
/* Body customization rules */
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

/* Navbar customization rules */
.navbar-nav > li a {
	font-family: "Abel", sans-serif;
	text-transform: uppercase;
	font-size: 1.4rem;
	text-decoration: none;
	color: aliceblue;
}

.navbar-nav > li a:hover {
	color: coral;
	font-weight: 600;
}

/* Carrousel customization rules */
.carousel-caption {
	bottom: 15% !important;
	display: flex;
}

.main-title {
	font-family: "Bangers", cursive !important;
	font-size: 5rem;
	color: aliceblue;
	-webkit-filter: drop-shadow(0 0 0.45rem crimson);
	        filter: drop-shadow(0 0 0.45rem crimson);
}

/* Cards Section customization rules */
.jumbotron {
	background-image: url(/static/media/headerBackground.ea5c64cf.jpg) !important;
	background-size: cover;
	height: 100%;
}

.card {
	transition: all 0.2s ease;
	cursor: pointer;
	padding: 0px;
}

.card:hover {
	opacity: 0.85;
	-webkit-transform: scale(0.99);
	        transform: scale(0.99);
}

.comic-thumb {
	width: 213px;
	height: 312px;
	aspect-ratio: auto 213/312;
}

.demi-title {
	font-family: "Bangers", cursive !important;
	font-size: 3.5rem;
	color: aliceblue;
	-webkit-filter: drop-shadow(0 0 0.45rem crimson);
	        filter: drop-shadow(0 0 0.45rem crimson);
}

.hero-name {
	font-family: "Georama", sans-serif;
	text-transform: capitalize;
	font-size: 1.5rem;
	font-weight: bold;
	color: cornflowerblue;
}

.text-justify {
	text-align: justify;
}

.centered {
	position: absolute;
	top: 15%;
	margin: 0 auto;
}

/*--------------------------------------------*/

.fade-enter {
	opacity: 0;
	-webkit-transform: scale(0.9);
	        transform: scale(0.9);
	-webkit-transform: translateX(-100px);
	        transform: translateX(-100px);
}
.fade-enter-active {
	opacity: 1;
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	transition: opacity 300ms, -webkit-transform 300ms ease;
	transition: opacity 300ms, transform 300ms ease;
	transition: opacity 300ms, transform 300ms ease, -webkit-transform 300ms ease;
}
.fade-exit {
	opacity: 1;
}
.fade-exit-active {
	opacity: 0;
	-webkit-transform: scale(0.9);
	        transform: scale(0.9);
	transition: opacity 300ms, -webkit-transform 300ms;
	transition: opacity 300ms, transform 300ms;
	transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

.thumb1 {
	height: 350px !important;
	overflow-y: hidden !important;
}

body {
	background-color: #020202;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

